.staffCard{  
    max-height: 351px;
    width: 350px;
    background: #ffffff;
    border-radius: 15px;
    border: none;
    overflow: hidden;
    padding-top: 20px;
    box-shadow:0px 24px 48px 0 rgba(0,0,0,0.1);
}
.itemstaff{
    height: 210px;
}
.staffCard .imgBx{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;

}

.staffCard  .staffBtn{
    padding: 5px 12px;
    margin-top: 15px;
    color:white;
    border: none;
    border-radius: 100px;
}
.staffGrid{
    display: grid;
    grid-gap: 2rem;
    padding: 1rem;
    max-width: 1250px;
    left:-90px;
    position: relative;

}

@media (min-width: 600px) {
    .staffGrid {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (min-width: 800px) {
    .staffGrid {
        grid-template-columns: repeat(5, 1fr);
    }
}